<template>
  <div id="app">
    <demonstration
        tag="h2"
        title="runtime "
        description=""
        :show-content="false">
    </demonstration>
    <demonstration
        title=""
        anchor="demo-code-title"
        :highlight="sourcecode"
        multiple>
      <template slot="description">
      </template>
      <template slot="source">
        <div class="source">
          <div class="vd-demo__block vd-demo__block-2">
            <runtime start="2017-10-14 12:00:00"></runtime>
          </div>
          <div class="vd-demo__block vd-demo__block-2">
            <runtime :start="new Date('2017-10-14 12:00:00')"></runtime>
          </div>
        </div>
      </template>
      <template slot="explanation">
         `start`  `Date` 
      </template>
    </demonstration>
    <demonstration
        title=""
        anchor="demo-code-title"
        :highlight="sourcecodeA">
      <template slot="description">
      </template>
      <template slot="source">
        <div class="source">
          <runtime start="2017-10-14 12:00:00" :units="{month: '', hour: '', minute: ''}"></runtime>
        </div>
      </template>
      <template slot="explanation">
         `units`  `year``month``day``hour``minute``second` 
      </template>
    </demonstration>
    <demonstration
        title=""
        anchor="demo-code-title"
        :highlight="sourcecodeB">
      <template slot="description">
      </template>
      <template slot="source">
        <div class="source">
          <runtime start="2017-10-14 12:00:00">
            <template slot="start">lalala</template>
            <template slot="end">hahaha</template>
            <template slot="time">20171014</template>
          </runtime>
        </div>
      </template>
      <template slot="explanation">
        `slot="start"``start-text` `slot="end"` `end-text` `slot="time"` 
      </template>
    </demonstration>
    <demonstration
        anchor="attributes"
        title="Attributes"
        :show-content="false">
      <template slot="table">
        <table class="vd-demo__table">
          <thead>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          </thead>
          <tbody>
          <tr>
            <td>start </td>
            <td></td>
            <td>string/date</td>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td>start-text</td>
            <td></td>
            <td>string</td>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td>end-text</td>
            <td></td>
            <td>string</td>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td>units</td>
            <td></td>
            <td>object</td>
            <td></td>
            <td>{year: '', month: '', day: '', hour: '', minute: '', second: ''}</td>
          </tr>
          </tbody>
        </table>
      </template>
    </demonstration>
    <demonstration
        anchor="slots"
        title="Slots"
        :show-content="false">
      <template slot="table">
        <table class="vd-demo__table">
          <thead>
          <th></th>
          <th></th>
          </thead>
          <tbody>
          <tr>
            <td>start</td>
            <td></td>
          </tr>
          <tr>
            <td>end</td>
            <td></td>
          </tr>
          <tr>
            <td>time</td>
            <td></td>
          </tr>
          </tbody>
        </table>
      </template>
    </demonstration>
  </div>
</template>

<script type="es6">
  import {sourcecode, sourcecodeA, sourcecodeB} from './template'

  export default {
    data () {
      return {
        msg: '',
        sourcecode,
        sourcecodeA,
        sourcecodeB
      }
    },
  }
</script>

<style lang="less" rel="stylesheet/less">
  #app {
    margin: 60px auto;
    .vd-demo__block {
      text-align: left;
    }
  }
</style>



